.navbar{
    background-color: #0A122A;
    /* opacity: 0.5; */
    height: 80px ;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 9999;
}
.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    z-index: 9999;
}

.nav-menu {
    background-color: #060b26;
    width: 250px ;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 9999;


}

.nav-menu.active{

    left: 0;
    transition: 350ms;
}

.nav-text{

    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text button{
    margin-left: 15%;
    margin-top: 15%;
}

.nav-text a{
    text-decoration: none;
    color: aliceblue;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover {

    background-color: gray;
}
.nav-menu-item{
    width: 100%;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content:flex-start;
    align-items: center;
}

span{
    margin-left: 16px;
}

